*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

body{
  min-height: 100vh;
  background-image: linear-gradient(to right, rgb(237, 179, 72), rgb(219, 116, 116));
}

h1{
  display: flex;
  min-height: 20vh;
  justify-content: center;
  align-items: center;
  color: whitesmoke;
}

.todo-input{
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-input form{
  display: flex;
  height: 5vh;
}

.todo-input form input{
  border: none;
  outline: none;
  background-color: whitesmoke;
  width: 40vw;
  padding-left: 10px;
}

.todo-input form button{
  border: none;
  outline: none;
  background-color: whitesmoke;
  color: rgb(219, 116, 116);
  width: 5vw;
  transition: all 0.3s ease;
  font-size: 1.2rem;
}

.todo-input form button:hover{
  border: none;
  outline: none;
  background-color: rgb(219, 116, 116);
  color: whitesmoke;
  width: 6vw;
}

.select{
  height: 5vh;
}

select{
  height: 5vh;
  border: none;
  margin-left: 1vw;
  background-color: whitesmoke;
  color: rgb(219, 116, 116);
}

.todo-list{
  display: flex;
}

.todo-element{
  list-style: none;
}

.todo-div{
  border: none;
  outline: none;
  background-color: whitesmoke;
  width: 40vw;
  height: 5vh;
  padding-left: 10px;
}

.todo-container{
  display: grid;
  place-content: center;
  margin-top: 20vh;
}

.todo-list{
  display: flex;
  flex-direction: column;
}

.todo{
  display: flex;
  margin-bottom: 1.5vh;
}

.todo-item{
  list-style: none;
  background-color: whitesmoke;
  min-width: 40vw;
  height: 5vh;
  display: flex;
  align-items: center;
  padding-left: 1vh;
}

.todo > button{
  width: 5vh;
  border: none;
  border-radius: 0;
  font-size: 1rem;
}

.delete-button{
  background-color: whitesmoke;
  color: red;
  transition: all 0.3s ease;
}

.delete-button:hover{
  background-color: red;
  color: whitesmoke;
}

.complete-button{
  background-color: whitesmoke;
  color: green;
  transition: all 0.3s ease;
}

.complete-button:hover{
  background-color: green;
  color: whitesmoke;
}

.fa-trash,
.fa-check{
  pointer-events: none;
}

.done{
  opacity: 50%;
  text-decoration: line-through;
  transition: all 0.5s ease;
}

.remove{
  transform: translateY(0);
  opacity: 1;
  animation: Remove 0.8s ease;
}

@keyframes Remove {
  to{
      opacity: 0;
      transform: translateY(100%) rotateX(90deg);
  }
}